@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.hint-block{
  position: relative;
  &._active{
    .tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.hint-btn{
  width: 1.5em;
  img{
    width: 100%;
  }
}

.tooltip{
  padding-top: 1.44em;
  position: absolute;
  top: 99%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__inner{
    color: $blue;
    width: 22.5em;
    border-radius: 1.25em;
    background: #FFF;
    box-shadow: 0 1.25em 3.125em 0 rgba(101, 118, 164, 0.25);
    padding: 1.25em 1.875em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/triangle.svg);
      @include bgImgSize(100%);
      left: 50%;
      top: -1.37em;
      transform: translateX(-50%);
      width: 2.6875em;
      height: 2.6875em;
    }
  }
}
